@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
/* DARK MODE STYLES */

#root {
  height: 100vh;
}

.App.dark {
  background-color: hsl(207, 26%, 17%);
  color: hsl(0, 0%, 100%);
}

/* DEFAULT STYLES */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  font-family: 'Nunito Sans', sans-serif;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  background: hsl(0, 0%, 97%);
  color: hsl(200, 15%, 8%);
}

.App {
  height: 100%;
}

.home {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.home.dark {
  background-color: hsl(207, 26%, 17%);
  color: hsl(0, 0%, 100%);
}

@media screen and (min-width: 1200px) {
  .grow { 
    transition: transform 0.2s ease-in-out; 
  }
  
  .grow:hover {
    transform: scale(1.05); 
  }
}
/* DARK MODE STYLES */
.navbar.dark {
  background-color: hsl(209, 23%, 22%);
  box-shadow: 0px 0px 4px 0px hsl(200, 15%, 8%);
  color: hsl(0, 0%, 100%);
}

.navbar.dark button,
.navbar.dark i {
  color: white;
  cursor: pointer;
}

/* DEFAULT STYLES */
.navbar {
  background: white;
  box-shadow: 0px 0px 4px 3px #eeeeee;
  color: hsl(200, 15%, 8%);
  display: flex;
    align-items: center;
    flex-wrap: flex;
    justify-content: space-between;
  font-size: 0.7rem;
  padding: 1.2rem 1rem;
}

.navbar #earth {
  height: 14px;
  margin-right: 5px;
  width: 14px;
}

.navbar button {
  border: none;
  background: hsla(0, 0%, 98%, 0%);
  font-size: 0.8rem;
  font-weight: 600;
}

.navbar i {
  color: hsl(200, 15%, 8%);
  margin: 0.5rem;
}

@media screen and (min-width: 600px) {
  .navbar {
    font-size: 1rem;
    padding: 1.5rem 4rem;
  }

}

@media screen and (min-width: 1200px) {
  .navbar {
    padding: 1.2rem 6rem;
  }
}

/* DARK MODE STYLES */
.country-card.dark {
  background-color: hsl(209, 23%, 22%);
  box-shadow: 0px 0px 4px 0px hsl(200, 15%, 8%);
  color: hsl(0, 0%, 100%);
}

/* DEFAULT STYLES */

.country-list {
  display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  grid-gap: 4rem;
  gap: 4rem;
  margin: 2rem 0;
}

.country-card {
  background: white;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 5px #eeeeee;
  color: hsl(200, 15%, 8%);
  overflow: hidden;
  width: 300px;
}

.country-card img {
  height: 150px;
  object-fit: cover;
  width: 300px;
}

.country-card .info-container {
  min-height: 230px;
  padding: 2rem;
  padding-bottom: 3rem;
}

.country-card .info-container h3 {
  margin-bottom: 1rem;
}

.country-card .info-container p {
  font-weight: 600;
  margin: 0.3rem 0;
}

.country-card .info-container span {
  font-weight: 400;
}

@media screen and (min-width: 400px) {
  .country-list {
    margin: 2rem 3rem;
  }
}

@media screen and (min-width: 1200px) {
  .country-list {
    margin: 3rem 6rem;
  }
}


/* DARK MODE STYLES */
.search.dark .search-bar-container,
.search.dark p,
.search.dark ul {
  background-color: hsl(209, 23%, 22%);
  box-shadow: 0px 0px 4px 0px hsl(200, 15%, 8%);
}

.search.dark .search-bar {
  background-color: hsl(209, 23%, 22%);
  color: white;
}

/* StackOverflow snippet to remove browser autofill background color */
.search.dark input:-webkit-autofill,
.search.dark input:-webkit-autofill:hover, 
.search.dark input:-webkit-autofill:focus, 
.search.dark input:-webkit-autofill:active{
  -webkit-box-shadow: 0 0 0 30px hsl(209, 23%, 22%) inset;
  -webkit-text-fill-color: white;
}

.search.dark .search-bar:-ms-input-placeholder {
  color: white;
}

.search.dark .search-bar::placeholder {
  color: white;
}

.search.dark .select-menu {
  box-shadow: 0px 0px 4px 0px hsl(200, 15%, 8%);
}

.search.dark .options-menu-container::before {
  border-top: 3px solid hsl(207, 26%, 17%);
}

.search.dark .option:hover {
  background: hsl(207, 26%, 17%);
}


/* DEFAULT STYLES */
/* Top-level styles */
.search {
  display: flex;
    justify-content: center;
  margin: 2rem 0;
}

.search-form {
  display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

/* Search bar styles */

.search-bar-container {
  background: white;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 3px #eeeeee;
  display: flex;
    align-items: center;
  height: 3rem;
  padding: 1rem 1.5rem;
  width: 300px;
}

.search-bar-container .search-bar:-ms-input-placeholder{
  font-size: 0.7rem;
  font-weight: 600;
}

.search-bar-container .search-bar::placeholder{
  font-size: 0.7rem;
  font-weight: 600;
}

.search-bar-container i {
  width: 2rem;
}

.search-bar-container .search-bar {
  border: none;
  padding: 0.5rem;
}

.search-bar-container input:focus {
  outline: none;
}

/* StackOverflow snippet to remove browser autofill background color */
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px white inset;
}

/* Custom Select Menu styles*/

.select-menu{
  box-shadow: 0px 0px 4px 3px #eeeeee;
  cursor: pointer;
  height: 3rem;
  position: relative;
  width: 200px;
}

.select-menu p, .select-menu ul {
  background-color: white;
  font-size: 0.7rem;
  font-weight: 500;
}

.select-menu p {
  border-radius: 5px;
  padding: 1rem;
  position: relative;
}

.select-menu i{
  position: absolute;
    top: 50%;
    right: 1rem;
  transform: translateY(-50%);
}

.options-menu-container::before{
  content: " ";
  display: block;
  border-top: 3px solid hsl(0, 0%, 97%);
}

.options-menu {
  border-radius: 5px;
  display: none;           
  list-style: none;
  padding: 0.5rem;
}

.active {
  display: flex;
    flex-direction: column;
  position: absolute;
  width: 200px;
}

/* this will be a click event toggle */
.select-menu:hover .options-menu .active {
}

.option {
  padding: 0.3rem;
}

.option:hover {
  background: hsl(0, 0%, 97%);
}

@media screen and (min-width: 600px) {

  .search {
    display: block;
    margin: 0 3rem;
  }

  .search-form {
    flex-direction: row;
    justify-content: space-between;
  }

}

@media screen and (min-width: 1200px) {
  .search {
    margin: 2rem 6rem;
  }
}
/* DARK MODE STYLE */
.country-page.dark .back-btn {
  background-color: hsl(209, 23%, 22%);
  box-shadow: 0px 0px 5px 0px hsl(200, 15%, 8%);
  color: hsl(0, 0%, 100%);
}

.country-page.dark .flag-container {
  box-shadow: 0px 0px 15px 0px hsl(200, 15%, 8%);
}

.country-page.dark .border-countries-container button {
  background-color: hsl(209, 23%, 22%);
  border: none;
  border-radius: 2px;
  box-shadow: 0px 0px 5px 0px hsl(200, 15%, 8%);
  color: hsl(0, 0%, 100%);
}

/* DEFAULT STYLES */
.country-page {
  display: flex;
    align-items: center;
    flex-direction: column;
}

.country-page-content {
  width: 300px;
}

.country-page .back-btn {
  background: hsl(0, 0%, 98%);
  border: none;
  border-radius: 2px;
  box-shadow: 0px 0px 4px 3px #dddddd;
  font-size: 0.8rem;
  font-weight: 600;
  margin: 2rem 0;
  padding: 0.3rem 1.2rem 0.2rem 1.2rem;
}

.country-page button i {
  margin-right: 10px;
  opacity: 0.8;
}

.country-page .back-btn span {
  opacity: 0.5;
}

@media screen and (min-width: 750px) {

  .country-page-content {
    width: 400px;
  }
}

@media screen and (min-width: 1200px) {
  .country-page-content {
    display: block;
  }
  
  .country-page .back-btn {
    margin: 4rem 0;
    padding: 0.5rem 1.7rem 0.4rem 1.5rem;
  }

  .country-page-content {
    margin: 0 5rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .country-details-container {
    height: 100%;
    display: flex;
      align-content: center;
  }
}
.country-details {
  margin: 1rem 0;
}

.country-details .flag-container img {
  height: 169px;
  object-fit: cover;
  width: 300px;
}

.country-details h3 {
  font-size: 1.2rem;
  margin: 2rem auto 1rem 0;
}

.general-info p,
.technical-info p {
  font-weight: 800;
  margin: 0.5rem 0;
  opacity: 0.7;
}

.general-info span,
.technical-info span {
  font-weight: 400;
}

.general-info p,
.technical-info p,
.general-info span,
.technical-info span {
  font-size: 0.8rem;
}

.technical-info {
  margin: 2rem 0;
}

.border-countries-container {
  display: flex;
  flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
  margin: 0.5rem 0;
  padding: 5px;
}

.border-countries-container button {
  background: hsl(0, 0%, 98%);
  border: none;
  border-radius: 2px;
  box-shadow: 0px 0px 2px 2px #dddddd;
  min-width: 70px;
  padding: 4px 10px;
  white-space: nowrap;
}

.border-countries-container button p {
  font-size: 0.6rem;
  font-weight: 600;
  opacity: 0.6;
}

@media screen and (min-width: 750px) {

  .country-details .flag-container img {
    height: 250px;
    object-fit: cover;
    width: 450px;
  }

}

@media screen and (min-width: 1200px) {
  .country-details {
    display: flex;
      grid-gap: 5rem;
      gap: 5rem;
    margin-top: 3rem;
  }

  .country-details .flag-container img {
    height: 350px;
    object-fit: cover;
    width: 500px;
  }

  .country-details .info-container {
    display: flex;
      flex-direction: column;
  }

  .country-details .info-container h3 {
    font-size: 1.6rem;
  }

  .demographics-container {
    display: flex;
      align-items: flex-start;
      grid-gap: 9rem;
      gap: 9rem;
    margin-bottom: 1rem;
    width: clamp(400px, 40vw, 800px);
  }

  .technical-info {
    margin: 0;
  }

  .border-countries {
    display: flex;
      align-items: center;
    margin-top: auto;
    margin-bottom: 1rem;
    max-width: 100%;
    padding-right: 5rem;
  }

  .border-countries h4 {
    align-self: center;
    font-size: 0.8rem;
    margin-right: 10px;
    opacity: 0.7;
    white-space: nowrap;
  }

}
